/* App.js 에 있는 코드들 */
.App {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  /* overflow: hidden; */
}
.bg-container {
  background: url("./img/background.png"); /*백그라운드 이미지 넣음*/
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
}
::-webkit-scrollbar-track {
  background: #000; /* 스크롤 트랙 배경 (검은색) */
}
::-webkit-scrollbar-thumb {
  background: #333; /* 스크롤바 색상 (어두운 회색) */
  border-radius: 5px;
}
/* Header.js 에 필요한것들 */
body {
  font-family: "Pretendard";
  margin: 0;
  padding: 0;
}
.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px; /* ✅ 여백 추가 */
  box-sizing: border-box; /* ✅ 패딩을 포함한 크기 유지 */
  height: 88px;
  margin-left: 13px;
}
.logo {
  width: 378px;
  height: 26px;
}
.headerright {
  display: flex;
  gap: 27px;
  font-size: 16px;
  align-items: center;
  color: white;
}
.language {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  font-family: "Pretendard"; /* 로드한 폰트를 사용 */
}
.headericon {
  width: 18px;
  height: 18px;
}
/* public 폴더 내에서 로컬 폰트 불러오기 */
@font-face {
  font-family: "Pretendard"; /* 원하는 폰트 이름 */
  src: url("../public/font/PretendardVariable.woff2") format("woff"); /* 폰트 파일 경로 */
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "futura"; /* 원하는 폰트 이름 */
  src: url("../public/font/FuturaCyrillicMedium.ttf") format("truetype"); /* 폰트 파일 경로 */
}
@font-face {
  font-family: "outfit"; /* 원하는 폰트 이름 */
  src: url("../public/font/OutfitVariableFont_wght1.ttf") format("truetype"); /* 폰트 파일 경로 */
  font-weight: normal;
  font-style: normal;
}
.MuiTypography-root {
  font-family: "Pretendard" !important; /* 임포탄트는 보다 권한 높은거임 mui 에서 저 root 앞에 있는게 고정적인 클래스라서 저걸로 불러온 것.*/
}
.Myinventory {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 6px;
}
/* 안에 컨텐츠 넣는거 스타일 정의의 */
.content {
  text-align: center;
}
/* 메인이미지 스타일 정의 */
.leftright{
  margin-top: 83px;
}
.leftright img{
  width: 396px;
  height: 508px;
}

