/* --------------------------------- 참가자격*/
.dlrjs{
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 60px;
}
.module-border-wrap {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  background: linear-gradient(-28deg, #E025CE,#00031A, #4AED52);
  padding: 3px;
  width: 1000px;
  border-radius: 10px;
  margin-bottom: 100px;
}
.module {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
  height: 186px;
  border-radius: 10px;
  width: 100%;
  background-color: #0e0d0d; 
}